import { NextRequest, NextResponse } from 'next/server'

export async function GET(request: NextRequest) {
  try {
    const searchParams = request.nextUrl.searchParams
    const name = searchParams.get('name')

    if (!name) {
      return NextResponse.json(
        { error: 'Player name is required' },
        { status: 400 }
      )
    }

    // Proxy request to Python backend
    const pythonBackendUrl = process.env.PYTHON_BACKEND_URL || 'http://localhost:8000'
    const apiUrl = `${pythonBackendUrl}/api/player-stats?player=${encodeURIComponent(name)}`
    
    console.log(`Proxying request to Python backend: ${apiUrl}`)
    
    const response = await fetch(apiUrl, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    })

    console.log('Python backend response status:', response.status)

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({ detail: 'Unknown error' }))
      console.error('Python backend error:', errorData)
      
      return NextResponse.json(
        { 
          error: errorData.detail || 'Failed to fetch player data from backend',
          success: false 
        },
        { status: response.status }
      )
    }

    const data = await response.json()
    console.log(`Successfully fetched data for ${data.count} player(s)`)
    
    return NextResponse.json(data, { status: 200 })
    
  } catch (error) {
    console.error('Error in Next.js API route:', error)
    return NextResponse.json(
      { 
        error: error instanceof Error ? error.message : 'Internal server error',
        success: false
      },
      { status: 500 }
    )
  }
}
